.hero {
  min-height: 100vh;
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
  position: relative;
  padding: 0 8% 8% 8%;
}

.hero-content {
  max-width: 1400px;
  width: 100%;
}

.hero-title {
  font-family: 'Inter', sans-serif;
  font-size: clamp(2.5rem, 6vw, 7rem);
  font-weight: 300;
  line-height: 1.1;
  letter-spacing: -0.02em;
}

.hero-title .line {
  display: block;
  transform-origin: left center;
  perspective: 1000px;
}

.hero-title .italic {
  font-family: 'Cormorant', Georgia, serif;
  font-style: italic;
  font-weight: 400;
}

@media (max-width: 768px) {
  .hero {
    padding: 0 5% 10% 5%;
  }
  
  .hero-title {
    font-size: clamp(2rem, 8vw, 3rem);
  }
}

@media (max-width: 480px) {
  .hero {
    padding: 0 5% 15% 5%;
  }
}
