.companies {
  width: 100%;
  overflow: hidden;
  padding: 4rem 0;
  background: rgba(0, 0, 0, 0.3);
  border-top: 1px solid rgba(255, 255, 255, 0.05);
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
  position: relative;
}

.companies::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 200px;
  height: 100%;
  background: linear-gradient(to right, rgba(0, 0, 0, 1), transparent);
  z-index: 2;
  pointer-events: none;
}

.companies::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 200px;
  height: 100%;
  background: linear-gradient(to left, rgba(0, 0, 0, 1), transparent);
  z-index: 2;
  pointer-events: none;
}

.companies-track {
  display: flex;
  align-items: center;
  width: 100%;
}

.companies-content {
  display: flex;
  align-items: center;
  gap: 8rem;
  padding: 0 4rem;
}

.company-item {
  flex-shrink: 0;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  filter: grayscale(100%) brightness(0) invert(1);
  opacity: 0.6;
  transition: all 0.3s ease;
}

.company-item:hover {
  filter: grayscale(0%) brightness(1) invert(0);
  opacity: 1;
  transform: scale(1.1);
}

.company-item img {
  height: 100%;
  width: auto;
  max-width: 150px;
  object-fit: contain;
}

@media (max-width: 768px) {
  .companies {
    padding: 2rem 0;
  }
  
  .companies-content {
    gap: 4rem;
    padding: 0 2rem;
  }
  
  .company-item {
    height: 40px;
  }
  
  .company-item img {
    max-width: 100px;
  }
  
  .companies::before,
  .companies::after {
    width: 100px;
  }
}
